  .history-container{
    padding-left: 0;
    margin-bottom: 0;
  }

  .history-container .history-item{
    display: block;
    padding-top: 0.8rem;
    padding-bottom: 0.1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    cursor: pointer;
  }

  .history-item:hover{
    background-color:#d7e8ff !important;
    border-radius: 0.8rem;
  }

  .history{
    color: #383d41 !important;
    background-color: #eef5ff!important;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
  }

  